import React, { useState } from 'react';
import '../CSS/Notice.css';

const Modal = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  const handleSubmit = () => {
    //      (:   )
    onClose();  //   
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <div className="modal-header">
          <h2>1</h2>
          <h2></h2>
          <button onClick={onClose} className="close-btn">X</button>
        </div>
        <div className="modal-body">
          <label></label>
          <input type="text" className="modal-input" />
          <label></label>
          <textarea className="modal-textarea"></textarea>
        </div>
        <div className="modal-footer">
          <button className="modal-delete-btn"></button>
          <div className="right-buttons">
            <button className="modal-save-btn"></button>
            <button className="modal-submit-btn" onClick={handleSubmit}></button>
          </div>
        </div>
      </div>
    </div>
  );
};

const Notice = () => {
  //  
  const dummyData = [
    { id: 1, number: '1', name: '9.19', user: '', date: '2024/10/23' },
    { id: 2, number: '2', name: '9.20', user: '', date: '2024/10/23' },
  ];

  //  
  const [rowsPerPage, setRowsPerPage] = useState(10);  //    
  const [currentPage, setCurrentPage] = useState(1);   //  
  const [isModalOpen, setIsModalOpen] = useState(false); //   

  //   
  const indexOfLastRow = currentPage * rowsPerPage;
  const indexOfFirstRow = indexOfLastRow - rowsPerPage;
  const currentRows = dummyData.slice(indexOfFirstRow, indexOfLastRow);

  //    
  const totalPages = Math.ceil(dummyData.length / rowsPerPage);

  //   
  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  //   
  const handleRowClick = () => {
    setIsModalOpen(true);  //   
  };

  return (
    <div className="notice-management">
      <main className="main-content">
        <div className="header">
          <h2> </h2>
        </div>

        <table className="notice-table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {currentRows.map((row) => (
              <tr key={row.id} onClick={handleRowClick}>
                <td>{row.number}</td>
                <td>{row.name}</td>
                <td>{row.user}</td>
                <td>{row.date}</td>
              </tr>
            ))}
          </tbody>
        </table>

        <div className="pagination">
          <button
            className="page-btn"
            onClick={() => handlePageChange(currentPage - 1)}
            disabled={currentPage === 1}
          >
            &lt;
          </button>
          {[...Array(totalPages)].map((_, index) => (
            <button
              key={index}
              className={`page-btn ${currentPage === index + 1 ? 'active' : ''}`}
              onClick={() => handlePageChange(index + 1)}
            >
              {index + 1}
            </button>
          ))}
          <button
            className="page-btn"
            onClick={() => handlePageChange(currentPage + 1)}
            disabled={currentPage === totalPages}
          >
            &gt;
          </button>
        </div>

        {/*    */}
        <Modal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)} />
      </main>
    </div>
  );
};

export default Notice;
